<template>
  <div class="section">
    <section class="container section-spacing">
      <div class="columns is-centered">
        <div class="column is-8">
          <h1 class="title is-1">May 2019 Partner Ticket Offer</h1>
        </div>
      </div>
    </section>

    <section class="container section-spacing content">
      <div class="columns is-centered">
        <div class="column is-7">
          <p class="is-size-4">
            Congratulations
            <span class="has-text-primary">{{ personName }}</span>
          </p>
          <p>
            You have recieved a partner invite for the TEDxAuckland event on May
            the 1st at Whoa! Studios for
            <template v-if="freeTicket">
              <strong class="has-text-primary"> free</strong>.
            </template>
            <template v-else>
              <strong class="has-text-primary">{{
                formattedTicketPrice
              }}</strong
              >.
            </template>
          </p>
          <p>
            During the first part of the evening you will be inspired and
            entertained by some of Aucklands top thought leaders. Following this
            will be social time with the speakers and a five course meal
            lovingly crafted with West Auckland produce by acclaimed and
            multi-award winning Kiwi chef,
            <strong class="has-text-primary">Ben Bayly</strong>
          </p>
          <p>
            This event is limited to 160 attendees and there is high demand.
            please accept
            <template v-if="!freeTicket">
              and purchase
            </template>
            the ticket or let the person who invited you know if you can't make
            it.
          </p>
          <p>
            We look forward to seeing you there and hope you enjoy the show.
          </p>
          <p>
            <strong class="has-text-primary">
              TEDxAuckland
            </strong>
          </p>
        </div>
      </div>
    </section>

    <section class="container">
      <div class="columns is-centered">
        <div class="column is-6">
          <form @submit.prevent="acceptTicket">
            <h2 class="title is-3">Your Contact Info</h2>
            <PersonInfo class="section-spacing" :person="person" :$v="$v" />

            <h2 class="title is-3">General Questions</h2>
            <ApplicationQuestions
              class="section-spacing"
              :details="applicationQuestions"
              :$v="$v"
            />

            <h2 class="title is-3">Questions for Ticket</h2>
            <TicketQuestions
              class="section-spacing"
              :questions="ticketQuestions"
              :$v="$v"
            />

            <div class="buttons has-addons is-right">
              <input
                class="button is-primary is-medium"
                type="submit"
                :value="iticketButtonText"
              />
            </div>
          </form>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import PersonInfo from "@/components/PersonInfo";
import ApplicationQuestions from "@/components/ApplicationQuestions";
import TicketQuestions from "@/components/TicketQuestions.vue";
import TicketService from "@/services/TicketService";
import NProgress from "nprogress";
import { personNameMixin } from "../mixins/personNameMixin";
import {
  personQuestionsMixin,
  personQuestionsValidations,
  applicationQuestionsMixin,
  applicationQuestionsValidations,
  ticketQuestionsMixin,
  ticketQuestionsValidations
} from "@/mixins/questionsMixin";

export default {
  name: "TicketOfferForPartner",
  components: {
    PersonInfo,
    ApplicationQuestions,
    TicketQuestions
  },
  props: {
    personInit: {
      type: Object,
      required: true
    },
    ticketOffer: {
      type: Object,
      required: true
    },
    event: {
      type: Object,
      required: true
    }
  },
  mixins: [
    personQuestionsMixin,
    applicationQuestionsMixin,
    ticketQuestionsMixin,
    personNameMixin
  ],
  computed: {
    formattedTicketPrice() {
      return `$${this.ticketOffer.costAfterDiscountCents / 100}`;
    },
    freeTicket() {
      return this.ticketOffer.discount === 100;
    },
    iticketButtonText() {
      return this.freeTicket
        ? "Claim through iTICKET"
        : "Purchase through iTICKET";
    }
  },
  validations: {
    person: personQuestionsValidations,
    applicationQuestions: applicationQuestionsValidations,
    ticketQuestions: ticketQuestionsValidations
  },
  methods: {
    acceptTicket() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        NProgress.start();
        TicketService.acceptTicket({
          id: this.ticketOffer.id,
          contactQuestions: this.person,
          ticketQuestions: {
            ...this.applicationQuestions,
            ...this.ticketQuestions
          }
        })
          .then(response => {
            NProgress.done();
            window.location.assign(response.data.purchase_url);
          })
          .catch(e => {
            NProgress.done();
            console.log("ERROR", e);
          });
      }
    }
  }
};
</script>
