<template>
  <div class="section">
    <section class="container section-spacing">
      <div class="columns is-centered">
        <div class="column is-8">
          <h1 class="title is-1">May 2019 Event Ticket Offer</h1>
        </div>
      </div>
    </section>

    <section class="container section-spacing content">
      <div class="columns is-centered">
        <div class="column is-7">
          <p class="is-size-4">
            Congratulations
            <span class="has-text-primary">{{ personName }}</span>
          </p>
          <p>
            We've accepted your application to buy a ticket for the TEDxAuckland
            event on May 1st. Please follow the link below to purchase your
            ticket for
            <strong class="has-text-primary">{{ formattedTicketPrice }}</strong
            >.
          </p>
          <p>
            The event will begin at 5pm at Whoa! Studios, where you will be
            inspired and entertained by our speakers. Networking with the
            speakers will follow, along with a five-course meal that's been
            crafted by multi-award winning Kiwi chef,
            <strong class="has-text-primary">Ben Bayly</strong>.
          </p>
          <p>
            Because of the limited tickets and large number of applications -
            you will need to purchase your ticket by
            <strong class="has-text-primary">
              {{ expirationDate.format("h:mm a") }}
            </strong>
            on the
            <strong class="has-text-primary">
              {{ expirationDate.format("Do [of] MMMM") }}
            </strong>
            , thats <strong class="has-text-primary">{{ timeLeft }}</strong>
            from now. After this time the opportunity to buy a ticket will be
            transferred to another applicant.
          </p>
          <p>
            We look forward to seeing you there!
          </p>
          <p>
            <strong class="has-text-primary">
              The TEDxAuckland team
            </strong>
          </p>
        </div>
      </div>
    </section>

    <section class="container">
      <div class="columns is-centered">
        <div class="column is-6">
          <form @submit.prevent="acceptTicket">
            <h2 class="title is-3">Your Contact Info</h2>
            <PersonInfo class="section-spacing" :person="person" :$v="$v" />

            <h2 class="title is-3">Questions for Ticket</h2>
            <TicketQuestions
              class="section-spacing"
              :questions="ticketQuestions"
              :$v="$v"
            />

            <div class="buttons has-addons is-right">
              <button
                @click.prevent="isRejectModalActive = true"
                class="button is-warning is-medium"
              >
                Give Up Ticket
              </button>

              <b-modal :active.sync="isRejectModalActive">
                <div class="modal-card center-modal">
                  <header class="modal-card-head">
                    <p class="modal-card-title">Give Up Ticket Offer</p>
                    <button
                      class="delete"
                      @click.prevent="isRejectModalActive = false"
                      aria-label="close"
                    ></button>
                  </header>
                  <section class="modal-card-body">
                    <p>Are you sure you want to give up your ticket offer?</p>
                    <p>
                      By clicking the button below you are giving up your
                      opportunity to buy a ticket and releasing it to another
                      applicant.
                    </p>
                  </section>
                  <footer class="modal-card-foot">
                    <button
                      @click.prevent="
                        rejectTicket();
                        isRejectModalActive = false;
                      "
                      class="button is-warning"
                    >
                      Give up Ticket
                    </button>
                    <button
                      @click.prevent="isRejectModalActive = false"
                      class="button"
                    >
                      Cancel
                    </button>
                  </footer>
                </div>
              </b-modal>

              <input
                class="button is-primary is-medium"
                type="submit"
                value="Purchase through iTICKET"
              />
            </div>
          </form>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import PersonInfo from "@/components/PersonInfo";
import TicketQuestions from "@/components/TicketQuestions.vue";
import TicketService from "@/services/TicketService";
import NProgress from "nprogress";
import moment from "moment";
import { personNameMixin } from "../mixins/personNameMixin";
import {
  personQuestionsMixin,
  personQuestionsValidations,
  ticketQuestionsMixin,
  ticketQuestionsValidations
} from "@/mixins/questionsMixin";

export default {
  name: "TicketOfferFromApplication",
  components: {
    PersonInfo,
    TicketQuestions
  },
  props: {
    personInit: {
      type: Object,
      required: true
    },
    ticketOffer: {
      type: Object,
      required: true
    },
    event: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      isRejectModalActive: false
    };
  },
  mixins: [personQuestionsMixin, ticketQuestionsMixin, personNameMixin],
  computed: {
    formattedTicketPrice() {
      return `$${this.ticketOffer.costAfterDiscountCents / 100}`;
    },
    expirationDate() {
      return moment(this.ticketOffer.iticket.expiration);
    },
    formattedExpirationDate() {
      return this.expirationDate.format("h:mm:ss a [on the] Do MMMM");
    },
    timeLeft() {
      return this.expirationDate.fromNow(true);
    }
  },
  validations: {
    person: personQuestionsValidations,
    ticketQuestions: ticketQuestionsValidations
  },
  methods: {
    acceptTicket() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        NProgress.start();
        TicketService.acceptTicket({
          id: this.ticketOffer.id,
          contactQuestions: this.person,
          ticketQuestions: this.ticketQuestions
        })
          .then(response => {
            NProgress.done();
            window.location.assign(response.data.purchase_url);
          })
          .catch(e => {
            NProgress.done();
            console.log("ERROR", e);
          });
      }
    },

    rejectTicket() {
      NProgress.start();
      TicketService.rejectTicket({
        id: this.ticketOffer.id
      })
        .then(() => {
          NProgress.done();
          this.$router.push({
            name: "ticket-offer-surrendered",
            params: { id: this.ticketOffer.id }
          });
        })
        .catch(e => {
          NProgress.done();
          console.log("ERROR", e);
        });
    }
  }
};
</script>
